header.navigation {
  z-index: 0;
  display: block;
  padding: 5px 30px;
  position: relative;
  @mixin atLarge {
    padding: 0 80px;
  }
}

.logo {
  height: 54px;
  width: 157px;
  overflow: hidden;
  display: inline-block;
  a {
    display: block;
    text-decoration: none;
    color: $primary-fg;
    font-weight: 500;
    position: relative;
    span {
      position: absolute;
      left: 60px;
      &.rams {
        top: 12px;
      }
      &.international {
        bottom: 12px;
      }
    }
  }
  img {
    width: 54px;
    height: 54px;
    background-size: cover;
  }
}

nav.primary-nav {
  margin-top: 0px;
  max-height: 0px;
  overflow: hidden;
  transition: ease all 300ms;
  &.expand-nav {
    margin-top: 10px;
    max-height: 1000px;
  }
  ul {
    list-style: none;
    padding-bottom: 10px;
    li {
      padding: 2px 0;
      border: 1px solid $divider;
      border-right: 0;
      border-left: 0;
      border-bottom: 0;
      &:last-child {
        border-bottom: 1px solid $divider;
      }
      a {
        display: block;
        text-decoration: none;
        font-weight: 400;
        color: $secondry-fg;
        padding: 5px 20px 7px 20px;
        position: relative;
        &.active {
          text-align: center;
          font-weight: 700;
          position: relative;
          &:after {
            position: absolute;
            right: 6.5px;
            bottom: 0;
            content: "";
            width: 66px;
            height: 2px;
            background: $primary-fg;
          }
          @mixin clearfix;
        }
        span.dropdown {
          font-size: 0.75rem;
          color: $secondry-fg;
        }
      }
      @mixin atLarge {
        display: inline-block;
      }
    }
    /* toggle logic */
    ul {
      margin-left: 20px;
      max-height: 0;
      overflow: hidden;
      transition: ease all 300ms;
      padding: 0;
      li {
        &:last-child {
          border-bottom: 0;
        }
      }
    }
  }
  @mixin atCommon {
    /* padding: 10.5px 0 10.5px 0; */
    overflow: visible;
    float: right;
    max-height: 54px;
    ul {
      float: right;
      li {
        border: none;
        &:last-child {
          border: none;
        }
        a {
          padding: 15.5px 17px 15.5px 17px;
        }
        ul {
          background: #fff;
          margin: 20px 0 0 0;
          padding: 0 15px 15px 15px;
          li {
            display: block;
            a {
              margin: 0;
              padding: 0;
            }
          }
        }
      }
    }
  }
}

nav.primary-nav ul li.contain-submenu.expand {
  ul {
    max-height: 650px;
  }
}

.hamburger {
  top: 1px;
  right: 25px;
  position: absolute;
  width: 50px;
  height: 50px;
  padding: 6px;
  border: 2px solid $primary-bg;
  margin: 5px 0;
  transform: scale(0.8);
  cursor: pointer;
  user-select: none;
  /* @mixin atLarge {
    display: none;
  } */
  .hamburger-bar {
    width: 100%;
    height: 2px;
    margin: 9px 0;
    display: block;
    background: $primary-bg;
    transition: all ease-in-out 200ms;
    &:first-child {
      margin: 4px 0;
    }
  }

  &.open {
    .hamburger-bar:first-child {
      margin-top: 0.75px;
      margin-left: 1.25px;
      transform-origin: 0% 50%;
      transform: rotate(45deg) scalex(1.3);
    }
    .hamburger-bar:nth-child(2) {
      transform: scale(0);
    }
    .hamburger-bar:last-child {
      margin-top: 18px;
      margin-left: 1.25px;
      transform-origin: 0% 50%;
      transform: rotate(-45deg) scalex(1.3);
    }
  }
  @mixin atCommon {
    display: none;
  }
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
}
